function flip(classname) {
  let parent = document.getElementsByClassName(classname)[0]

  let upper = parent.children[0].children[0]
  let upperText = upper.children[0].children[0]
  let upperBack = parent.children[0].children[1]
  let lower = parent.children[1].children[0]
  let lowerText = upper.children[0].children[0]
  let lowerBack = parent.children[1].children[1]

  upper.style.transform = "rotateX(180deg)"
  lowerBack.style.transform = "rotateX(360deg)"

}

setTimeout(function() {flip("second")}, 1000)
setTimeout(function() {flip("minute")}, 2000)
setTimeout(function() {flip("hour")}, 3000)
setTimeout(function() {flip("day")}, 4000)
